"use client";

import RotatingText from "./RotatingText";
import { motion } from "framer-motion";

export default function HeroOverlay() {
  return (
    <motion.div
      className="absolute inset-0 z-10 flex flex-col items-center justify-center px-4 md:px-8 text-center pointer-events-none"
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      transition={{ duration: 1.5 }}
    >
      {/* Tagline / Lead text */}
      <motion.h2
        initial={{ y: 30, opacity: 0 }}
        animate={{ y: 0, opacity: 1 }}
        transition={{ duration: 1 }}
        className="text-base md:text-xl tracking-wide text-white/80 font-light uppercase mb-4"
      >
        Crafting Timeless Wedding Stories
      </motion.h2>

      {/* Main heading: Static + Rotating side by side */}
      <div className="flex flex-wrap items-center justify-center gap-3 mb-4">
        <h1 className="text-4xl sm:text-6xl md:text-7xl font-extrabold text-transparent bg-clip-text bg-gradient-to-r from-pink-500 to-rose-400 tracking-tight drop-shadow-xl">
          We Capture
        </h1>
        <RotatingText
          texts={["Moments ✨", "Emotions 💖", "Memories 💍", "Love Stories 🎥"]}
          splitBy="words"
          mainClassName="text-4xl sm:text-6xl md:text-7xl font-extrabold text-white drop-shadow-[0_2px_10px_rgba(255,0,120,0.5)]"
          splitLevelClassName="gap-x-2"
          elementLevelClassName="inline-block"
          staggerDuration={0.12}
          rotationInterval={2500}
          loop
          auto
        />
      </div>

      {/* Supporting subtitle */}
      <p className="mt-3 text-white/70 text-sm sm:text-lg md:text-xl max-w-xl pointer-events-auto font-light leading-relaxed">
        Visual poetry through lenses — wedding photography, cinematography, and timeless edits tailored to your story.
      </p>

      {/* CTA Button */}
      <motion.button
        initial={{ scale: 0.95, opacity: 0 }}
        animate={{ scale: 1, opacity: 1 }}
        transition={{ delay: 1.2, duration: 0.6 }}
        className="mt-8 px-8 py-3 bg-gradient-to-r from-pink-600 to-rose-500 text-white text-base sm:text-lg rounded-full shadow-xl hover:scale-105 hover:shadow-2xl transition-transform duration-300 pointer-events-auto"
      >
        Book Your Special Day →
      </motion.button>
    </motion.div>
  );
}
